.technical-skill-section{
    overflow: hidden;
    background-image: linear-gradient(to bottom, #000, #383838, #363636);
    border-top: 1px solid #000;
    width: 100%;
    padding-bottom: 100px;
    box-shadow: 0px 10px 2px #636363;
}

.tech-container{
    width: 100%;
}
/* heading */
.tech-header {
    width: 100%;
    padding-top: 50px;
}
.tech-title {
    display: flex;
    justify-content: center;
    text-align: center;
    width: 50%;
    transform: skew(10deg, 3deg);
}
.tech-subtitle {
    display: flex;
    justify-content: center;
    text-align: center;
    width: 50%;
    margin-left: 550px;
    margin-top: 25px;
    transform: skew(10deg, 3deg);
}
.tech-title h1 {
    padding-top: 5px;
    padding-bottom: 5px;
    padding-left: 100px;
    padding-right: 100px;
    font-family: 'Bodoni Moda', serif;
    font-size: 2vw;
    font-weight: 400;
    letter-spacing: 8px;
    background: #f1e868;
    box-shadow: 2px 5px 2px #666229;
}
.tech-subtitle h1 {
    padding-top: 5px;
    padding-bottom: 5px;
    padding-left: 100px;
    padding-right: 100px;
    font-family: 'Bodoni Moda', serif;
    font-size: 2vw;
    font-weight: 400;
    letter-spacing: 4px;
    background: #a19b46;
    box-shadow: 2px 5px 2px #596629;
}
/* skill bubbles */
.f-skills {
    border-radius: 50%;
    padding-top: 30px;
    padding-bottom: 30px;
    padding-left: 40px;
    padding-right: 40px;
    box-shadow: inset -12px -12px 8px #8080808f, inset 4px 10px 10px #b8b8b8;
    background: #fff;
    z-index: 2;
    transform: scale(0.7);
    transition-duration: 1500ms;
}
.f-skills:hover {
    transition-duration: 500ms;
    box-shadow: inset -12px -12px 8px #8080808f, inset 4px 10px 10px #b8b8b8, 2px 2px 18px 5px #81818186;
    transform: scale(1.0) !important;
}
.f-skills img {
    border-radius: 50px;
    width: 10vw;
    height: 20vh;
}
.skill-p{
    font-family: 'Bodoni Moda', serif;
    font-weight: 700;
    color: #292929;
}

/* progress bars */
.tech-skill-progress {
    width: 100%;
    height: auto;
    font-family: 'Bodoni Moda', serif;
    font-size: 1.1vw;
    font-weight: 400;
    color: #fff;
}
.progress {
    height: 15px !important;
    box-shadow: 2px 3px 2px #8e9091fa;
}
.progress:hover {
    transform: scale(1.01) !important;
}

#progress-React:hover {
    box-shadow: 2px 2px 10px #0eb8ecfa;
}
#progress-Html:hover {
    box-shadow: 2px 2px 10px #2b5adb;
}
#progress-Css:hover {
